<template>
  <div class="card">
    <div class="card-header">
      Exam02TemplateRefs
    </div>
    <div class="card-body">
      <div>
        <h6>[DOM 요소 참조]</h6>
        <p>data: {{data}}</p>
        <button @click="setInputValue" class="btn btn-info btn-sm mr-2">데이터 변경</button> 
        <input type="text" ref="inputTag" name="mid"/> <!-- 포커스 사용을 위한 ref -->
      </div>

      <hr />

      <div>
        <h6>[자식 컴포넌트 참조]</h6>
        <p>child data: {{(childRef !== null)? childRef.data: ""}}</p> <!-- Ref가 되면 중간에 .value를 쓰지 않아도 된다-->
        <p>
          <button @click="changeChildData" class="btn btn-info btn-sm mr-2">자식 데이터 변경</button> 
          <button @click="callChildMethod" class="btn btn-info btn-sm mr-2">자식 메소드 호출</button> 
        </p>
        <Child ref="childRef"/> <!--child 컴포넌트를 참조하는 반응형 속성-->
      </div>
    </div>
  </div>
</template>
<!--  template 의 해석 방향은 위에서 아래이다. 참조하는 시점보다 더 빠르게 호출하면 호출되지 않는다 -->


<script setup>
import { ref, onMounted } from "vue";
import Child from "./Child";

const data = ref("summer");
const inputTag = ref(null);
const childRef = ref(null);

onMounted(()=> {
  console.log(inputTag.value);
  inputTag.value.focus(); //vue에서는 focus함수를 제공하지 않는다. 참조를 얻어서 focus를 실행

  console.log(childRef);
});

function setInputValue() {
  inputTag.value.value = data.value; //처음 컴포넌트가 생성되고 나서 onMounted를 사용할 수 있다
}

function changeChildData() { //child의 허락하에 외부 컴포넌트에서 데이터를 변경할 수 있다. 하지만 좋은 방법이 아니기 때문에 이 방법은 사용하지 말아야 한다
  childRef.value.data += 1;
}

function callChildMethod() {
  childRef.value.method(); //child 컴포넌트의 의지와 상관없이 바깥쪽에서 child 컴포넌트를 억지로 사용
}


</script>

<style scoped>
</style>
