<template>
  <div class="card">
    <div class="card-header">
      Child
    </div>
    <div class="card-body">
      <p>data: {{data}}</p>
    </div>
  </div>
</template>

<script setup>
import { ref, defineExpose } from "vue"; //defineExpose 는 자신의 데이터를 외부에서 사용할 수 있도록 노출 시킨다

const data = ref(10);

function method() {
  data.value -= 1;
}

defineExpose({
  data,
  method
})

</script>

